<script>
import IndexNavbar from "@/components/Navbars/IndexNavbar.vue";
import FooterComponent from "@/components/Footers/Footer.vue";

import patternVue from "@/assets/img/pattern_vue.png";
import logo_smp from "@/assets/img/logo.png";
import data_kelulusan from '@/data/data.json';

export default {
  data() {
    return {
      patternVue,
      logo_smp,
      data_kelulusan,
      onSubmit: false,
      form:{
          nisn : null,
          nis : null,
      },
    };
  },
  components: {
    IndexNavbar,
    FooterComponent,
  },
  methods: {
    goto(refName) {
      var element = this.$refs[refName];
      var top = element.offsetTop;
      window.scrollTo(0, top);
    },
    submit() {
      // console.log(this.getDateTime());
      // let tgl = new Date().toLocaleDateString();
      let tgl = this.getDateTime();
      if (tgl == 'alert') {
        this.$swal({
          title : "Perhatian",
          html : "Pengumuman kelulusan dapat diakses <br>pada tanggal <b>15 Juni 2022 10:00 WIB</b> <br>sampai <b>16 Juni 2022 23:59 WIB.</b>",
          icon : "warning",
          timer: 10000,
        });
      } else {
        if (this.form.nisn == null || this.form.nis == null) {
          this.$swal(
            'Peringatan',
            'Pastikan anda telah mengisi inputan',
            'error'
          );
        } else {
          let cekData = data_kelulusan.find(m => m.nisn.includes(this.form.nisn));
          if (cekData.nisn == this.form.nisn && cekData.nis == this.form.nis) {
            // let jk = cekData.jenis_kelamin == 'P' ? "<span class='fas fa-female' > </span>" : "<span class='fas fa-male' > </span>"
            this.$swal({
              // title : "",
              html: "<b style='font-size:25px'>" + cekData.nama.toUpperCase() + " [" + cekData.nisn + " - " + cekData.nis +"] </b><br><br> <span style='font-size:21px'> Dinyatakan <b>LULUS</b> dari Satuan Pendidikan <br>berdasarkan Surat Keputusan Kepala SMP Negeri 1 Diwek <br>Nomor: <b>420/1132.4/415.16.2.33/2021</b> tanggal <b>12 Juli 2021</b> <br>tentang Kriteria Kelulusan Peserta Didik Pada Satuan Pendidikan SMP Negeri 1 Diwek Tahun Pelajaran 2021/2022.</span>",
              icon: "success",
              // timer: 10000,
              width: 900,
            });
          } else {
            this.$swal(
              'Perhatian',
              'NISN / NIS tidak sesuai',
              'warning'
            );
          }
        }
      }
    },
    getDateTime(){
      var date_now = new Date();
      // var date_start = new Date('Jun 02 2021 11:14:00');
      // var date_end = new Date('Jun 02 2021 11:15:00');
      var date_start = new Date('Jun 15 2022 10:00:00');
      var date_end = new Date('Jun 16 2022 23:59:00');
      if(date_now.getTime() >=date_start.getTime() && date_now.getTime() <= date_end.getTime()){
          return "ok";
      } else {
          return "alert";
      }

    },
    checkAccess(){
      // let tgl = new Date().toLocaleDateString();
      let tgl = this.getDateTime();
      if (tgl == 'alert') {
        this.$swal({
          title : "Perhatian",
          html : "Pengumuman kelulusan dapat diakses <br>pada tanggal <b>15 Juni 2022 10:00 WIB</b> <br>sampai <b>16 Juni 2022 23:59 WIB.</b>",
          icon : "warning",
          timer: 10000,
        });
      }
    }
  },
  mounted(){
    this.checkAccess()
  }
};
</script>

